#contact__me{
      width: auto;
       min-height: 100vh;
       margin-top: 40px;
       margin-bottom: 52px;
       text-transform: uppercase;
       display: block;
       position: relative;
      

   }
   
   #contact__me h1{
      color: #7b7777;
     font-weight: bold;
     font-size: 25px;
     
   }
 
   #contact__me img{
      width: 250px;
      height: 250px;
      border: 2px solid #e7e7e736;
      border-radius: 50%;;
      -webkit-border-radius: 50%;
      -moz-border-radius: 50%;
      -ms-border-radius: 50%;
      -o-border-radius: 50%;
}


   #contact__me caption.mr{
      margin-top: 5px;
      font-weight: bold;
      position: relative;
      display: inline-block;
      color: #ce4b7c;;
      cursor: pointer;
  
   }

   p.display_details{
      display: none;
    background: #433;
    color: #fff;
    padding: 20px;
   }
  
   #contact__me .fa-envelope-o,
   #contact__me .fa-phone{
    font-size: 25px;
    margin-right: 30px;
    color: #fdfdfd;
    background: #ce4b7c;
    padding: 0px;
    border-radius: 50px;
    width: 45px;
    height: 45px;
    line-height: 45px;
    text-align: center;

    filter: alpha(opacity=70);
    transition: all .2s ease-in-out;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -ms-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    cursor: pointer;
    opacity: .8;
   }

   #contact__me .fa-envelope-o:hover {
      transform: rotate(360deg);
      -moz-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
      -webkit-transform: rotate(360deg);
      -o-transform: rotate(360deg);
  }
  #contact__me .fa-phone:hover {
   transform: rotate(360deg);
   -moz-transform: rotate(360deg);
   -ms-transform: rotate(360deg);
   -webkit-transform: rotate(360deg);
   -o-transform: rotate(360deg);
}
     




   ul.social__media li{
    display: inline-block;
    margin-right: 16px;
    color: #444;
    font-size: 12px;
  
   }
   ul.social__media li .fa{
    color: #ff0361;
   }
   ul.social__media li a{
    color: #000;
    border-bottom: 2px solid #eaeaea;
    border-width: 2px;
    transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    -moz-transition: all .4s ease-in-out;
    -ms-transition: all .4s ease-in-out;
    -o-transition: all .4s ease-in-out;
    
}
 

   ul.social__media li a:hover {
      border-bottom: 2px solid #cf4b7c;
      
      text-decoration: none;
      letter-spacing: 1px;
  }
.topnav {
  overflow: hidden;
  background: #870000; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#190a05),
    to(#870000)
  );
  background: linear-gradient(
    to right,
    #190a05,
    #870000
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.topnav a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.topnav a.active {
  background: none;
  color: white;
}

.topnav .icon {
  display: none;
}

@media screen and (max-width: 600px) {
  .topnav a:not(:first-child) {
    display: none;
  }
  .topnav a.icon {
    float: right;
    display: block;
  }
}

@media screen and (max-width: 600px) {
  .topnav.responsive {
    position: relative;
  }
  .topnav.responsive .icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
}

